import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Form, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  dispatchClose,
  EditViewProps,
  ErroList,
  ERROR,
  FileInputPreview,
  InputGroupInline,
  InputTooltipProps,
  ProcessBar,
  progressActions,
  showToast,
  SIZE_ONE_HUNDRED_BYTES,
  WindowComponent,
  withTooltip,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { RuleMessage } from '../../../../models/dtos/ger/ruleMessage';
import { ImportacaoLancamentoCSVParam } from '../../../../models/dtos/mcg/importacaoLancamentoCSVParam';
import { importarLancamentoCSV } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import ResultadoIntegracao from '../../../components/resultadoIntegracao/ResultadoIntegracao';

interface ImportacaoLancamentosCSVProcessViewProps extends EditViewProps<ImportacaoLancamentoCSVParam> {}

const ImportacaoLancamentosCSVProcessView: FC<ImportacaoLancamentosCSVProcessViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const [showResultView, setShowResultView] = useState(false);

  const [listResult, setListResult] = useState<RuleMessage[]>([]);

  const dispatch = useDispatch();

  const { reset, ...formProps } = useForm<ImportacaoLancamentoCSVParam>({ mode: 'onBlur' });

  const MAX_FILES: number = 100;

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa !== null) {
        const { empresa } = globalReducer.globalParameter;
        setTimeout(() => {
          reset({
            empresa: {
              id: empresa.id,
            },
          });
        }, 500);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onClose = (goBack: boolean = true) => {
    dispatchClose(props, props.history, goBack);
  };

  const handleCancel = () => onClose();

  const handleImport = (data: ImportacaoLancamentoCSVParam) => {
    const erros = validateExtras();
    if (erros.length > 0) {
      const params: AlertProps = {
        message: <ErroList erros={erros} />,
        type: AlertType.WARNING,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);
    } else {
      dispatch(
        progressActions.showProgress({
          animated: true,
          className: 'progress-bar-rounded progress-animated-alt',
          color: 'first',
          label: 'Aguarde processando...',
          striped: true,
          value: 100,
        })
      );
      callImport(data);
    }
  };

  function callImport(data: ImportacaoLancamentoCSVParam): void {
    importarLancamentoCSV(data, {
      errorFunction: mensagem => {
        dispatch(progressActions.hideProgress());
        showToast(mensagem, ERROR);
      },
      thenFunction: (result: RuleMessage[]) => {
        dispatch(progressActions.hideProgress());
        setListResult(result);
        setShowResultView(true);
      },
    });
  }

  function validateExtras(): string[] {
    const erros = [];
    if (formProps.getValues().arquivos.length === 0) {
      erros.push('Nenhum Arquivo selecionado');
    }

    return erros;
  }

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'top-start',
    };
  };

  return (
    <WindowComponent
      title="Importação de Lançamentos por Planilha do Excel (Formato CSV)"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
      wikiId={1546}
    >
      {showResultView ? (
        <ResultadoIntegracao
          {...props}
          errorList={listResult.filter(it => !it.success)}
          successList={listResult.filter(it => it.success)}
          hidePrintButton={true}
          tabFailure="Registros Rejeitados"
          tabSuccess="Registros Aceitos"
        />
      ) : (
        <Form>
          <InputGroupInline formProps={formProps} id="empresa.id" hidden={true} />
          <Row className="mb-3">
            <Col>
              <CustomInput
                id="idimportarDuplicados"
                innerRef={formProps.register}
                label="Importar Lançamentos Duplicados"
                name="importarDuplicados"
                type="switch"
              />
              {withTooltip(
                propToolTip(
                  'Por padrão, o Calima considera os registros duplicados e não realiza a importação dos mesmos. Marcando esta opção, as linhas duplicadas serão importadas.'
                ),
                'importarDuplicados'
              )}
            </Col>
            <Col>
              <CustomInput
                id="idimportarLancamentosCardinal"
                label="Importar Lançamentos por lote (2ª, 3º e 4ª fórmula de escrituração contábil)"
                name="importarLancamentosCardinal"
                innerRef={formProps.register}
                type="switch"
              />
              {withTooltip(
                propToolTip(
                  'Por padrão, o Calima importa somente Lançamentos do tipo um Débito para um Crédito. Marcando esta opção é possível efetuar importação pelas fórmulas de escrituração ‘2ª - Um Débito para vários Créditos’, 3ª - Um Crédito para vários Débitos’ ou ‘4ª - Vários Débitos para vários Créditos’. Lembre-se que os lançamentos devem possuir mesmo número de lote e a mesma data de lançamento para serem válidos.'
                ),
                'importarLancamentosCardinal'
              )}
            </Col>
          </Row>

          <Row>
            <Col>
              <FileInputPreview
                acceptedTypes=".csv"
                formProps={formProps}
                isMultiple={true}
                limitSizeBytes={SIZE_ONE_HUNDRED_BYTES}
                propImageName="arquivos"
                maxFiles={MAX_FILES}
              />
            </Col>
          </Row>
          <ProcessBar handleSubmit={formProps.handleSubmit(handleImport)} handleClose={handleCancel} />
        </Form>
      )}
    </WindowComponent>
  );
};

export default ImportacaoLancamentosCSVProcessView;
